<template>
  <div id="bottomFooter">
    <mu-container
      style="max-width: 100vw; width:100vw;"
      id="bottomFooter"
      v-for="(item,index) in list1"
      :key="index"
    >
      <!-- to="/Home" -->
      <!-- to="/DayControl" -->
      <!--  to="/MonitorPoint" -->
      <!-- to="/Control" -->
      <!--  to="/Settings" -->
      <mu-bottom-nav :value.sync="shift" @change="change">
        <mu-bottom-nav-item
          value="0"
          title="总览"
          icon="language"
          v-show="menuArr.HomeStatus"
        ></mu-bottom-nav-item>
        <mu-bottom-nav-item
          value="1"
          title="日常管理"
          icon="event"
          v-show="menuArr.DayStatus"
        ></mu-bottom-nav-item>
        <mu-bottom-nav-item value="2" title="监控点" icon="visibility" v-show="show"></mu-bottom-nav-item>
        <mu-bottom-nav-item
          value="3"
          title="运维管理"
          icon="build"
          v-show="menuArr.RunControlStatus"
        ></mu-bottom-nav-item>
        <mu-bottom-nav-item
          value="4"
          title="设置"
          icon="settings"
          v-show="menuArr.systemStatus"
        ></mu-bottom-nav-item>
      </mu-bottom-nav>
    </mu-container>
  </div>
</template>
<script>
export default {
  data() {
    return {
      show: false,
      msg: "hello",
      list1: [{ name: "总览", icon: "language" }],
      // shift: "0",
      menuArr: [
        {
          HomeStatus: false,
          DayStatus: false,
          RunControlStatus: false,
          systemStatus: false,
          realData: false,
          warningControl: false,
          eventControl: false,
          deviceControl: false,
          deviceAbnormal: false
        }
      ],
      menu2: [],
      msg: "hello 数据详情",
      shift: "0"
      // list1: [{ name: "总览", icon: "language" }]
    };
  },
  props: {
    shiftProps: String
  },
  methods: {
    change(e) {
      //   <!-- to="/Home" -->
      // <!-- to="/DayControl" -->
      // <!--  to="/MonitorPoint" -->
      // <!-- to="/Control" -->
      // <!--  to="/Settings" -->
      console.log(e);

      if (e == "0") {
        this.$router.replace("/Home");
      } else if (e == "1") {
        this.$router.replace("/DayControl");
      } else if (e == "2") {
        this.$router.replace("/MonitorPoint");
      } else if (e == "3") {
        this.$router.replace("/Control");
      } else if (e == "4") {
        this.$router.replace("/Settings");
      }

      this.shift = e; // 这里的val输出是导航栏的下标
      // 使用任何一种存储状态的方法，vuex也好，我这里使用sessionStorage
      sessionStorage.setItem("navItem", e); //存储状态
    },
    initmenu() {
      var m = [];
      var menu1 = sessionStorage.getItem("user");
      m = menu1.split(",");
      console.log(m);
      this.menu2.push(m);
      console.log("user111", this.menu2);
      for (let index = 0; index < this.menu2.length; index++) {
        this.Menu = this.menu2[index];
        console.log("32", this.Menu);
      }
      var a = [];
      a = this.Menu;
      console.log("hello", a);
      if (a.indexOf("实时数据") != -1) {
        this.menuArr.realData = true;
      } else {
        this.menuArr.realData = false;
      }
      if (a.indexOf("预警管理") != -1) {
        this.menuArr.warningControl = true;
      } else {
        this.menuArr.warningControl = false;
      }
      if (a.indexOf("事件管理") != -1) {
        this.menuArr.eventControl = true;
      } else {
        this.menuArr.eventControl = false;
      }
      if (a.indexOf("设备异常管理") != -1) {
        this.menuArr.deviceControl = true;
      } else {
        this.menuArr.deviceControl = false;
      }
      if (a.indexOf("设备异常统计") != -1 && this.user == "运维人员") {
        this.menuArr.deviceAbnormal = true;
        // console.log(this.user)
      } else {
        this.menuArr.deviceAbnormal = false;
      }
    },
    Data() {
      //   alert(1);
      this.$router.replace("/RealData");
    },
    calls() {
      this.$router.replace("/WarningControl");
    },
    event() {
      this.$router.replace("/Event");
    },
    equipment() {},
    device() {
      this.$router.replace("/Data");
    },
    menu() {
      this.menuArr.HomeStatus = sessionStorage.getItem("HomeStatus");
      if (this.menuArr.HomeStatus == "false") {
        this.menuArr.HomeStatus = false;
      } else {
        this.menuArr.HomeStatus = true;
      }
      console.log(this.menuArr.HomeStatus);
      this.menuArr.DayStatus = sessionStorage.getItem("DayStatus");
      if (this.menuArr.DayStatus == "false") {
        this.menuArr.DayStatus = false;
      } else {
        this.menuArr.DayStatus = true;
      }
      this.menuArr.RunControlStatus = sessionStorage.getItem(
        "RunControlStatus"
      );
      if (this.menuArr.RunControlStatus == "false") {
        this.menuArr.RunControlStatus = false;
      } else {
        this.menuArr.RunControlStatus = true;
      }

      this.menuArr.systemStatus = sessionStorage.getItem("systemStatus");
      if (this.menuArr.systemStatus == "false") {
        this.menuArr.systemStatus = false;
      } else {
        this.menuArr.systemStatus = true;
      }
    }
  },

  created() {
    this.user = sessionStorage.getItem("lastname");
    this.menu();
    this.initmenu();

    console.log("0", sessionStorage.getItem("navItem"));
    if (sessionStorage.getItem("navItem") == null) {
      this.shift = "0";
    } else {
       this.shift = sessionStorage.getItem("navItem");
    }
  },
  mounted() {
    // this.shift = sessionStorage.getItem("navItem");
    // setInterval(() => {
    //   this.shift = this.shiftProps;
    // }, 2000);
  },
  // watch: {
  //   "$route.path": function(newVal, oldVal) {
  //     console.log(newVal);
  //     if (newVal === "/DayControl") {
  //       this.shift = "1";
  //       console.log(11111111111112);
  //     } else {
  //       console.log(1111111111111);
  //     }
  //   }
  // }
};
</script>
<style>
#bottomFooter {
  position: fixed;
  bottom: 0;
  padding: 0;
  margin-right: 5px;
}
</style>



