<template>
  <div>
    <mu-appbar style="width: 100%;" color="primary" class="header">
      <span>监控点</span>
      <mu-menu slot="right">
        <mu-button flat @click="reset">
          <mu-icon value="loop"></mu-icon> 
        </mu-button>
        
      </mu-menu>
    </mu-appbar>
    <div class="header1">
      <div class="title">
        <span>餐饮油烟在线监控</span>
      </div>
      <div class="number">
        <p>{{number}}</p>
      </div>
      <div>
        <span>接入监控点数</span>
      </div>
    </div>
    <div class="content">
      <mu-row>
        <mu-col span="1"></mu-col>
        <mu-col span="9">
          <mu-form
            :model="form"
            class="mu-demo-form"
            :label-position="labelPosition"
            label-width="100"
          >
            <!-- <mu-form-item prop="input" label>
              <mu-text-field v-model="form.input">
                <mu-icon value="search" @click="search()"></mu-icon>
              </mu-text-field>
            </mu-form-item>-->
            <mu-select label="" filterable v-model="filterable.value1" full-width @change="search">
              <mu-option v-for="(city,index) in citys" :key="index" :label="city" :value="city"></mu-option>
            </mu-select>
          </mu-form>
        </mu-col>
        <mu-col span="2"></mu-col>
      </mu-row>
    </div>
    <div class="dataContent">
      <div class v-for="(item,index) in list" :key="index">
        <div class="title1">
          <span>{{item.monitoringpoint}}</span>
        </div>
        <div>
          <mu-row>
            <mu-col span="5" offset="2">
              <div>
                <span>公司：</span>
                <span>{{item.restaurant}}</span>
              </div>
            </mu-col>
            <mu-col span="5" offset>
              <div>
                <span>区域：</span>
                <span>{{item.area}}</span>
              </div>
            </mu-col>
            <mu-col span="5" offset="2">
              <div>
                <span>菜系：</span>
                <span>{{item.foodType}}</span>
              </div>
            </mu-col>
            <mu-col span="5" offset>
              <div>
                <span>街道：</span>
                <span>{{item.street}}</span>
              </div>
            </mu-col>
            <mu-col span="5" offset="2">
              <div>
                <span>设备号：</span>
                <span>{{item.devicenoname}}</span>
              </div>
            </mu-col>
            <mu-col span="5" offset>
              <div>
                <span>状态：</span>
                <span :style="color" v-if="(item.di0==1)&&(item.di1==1)">正常</span>
                <span
                  :style="color"
                  v-if="((item.di0!=1)&&(item.di1==1))||((item.di0==1)&&(item.di1!=1))"
                >异常</span>
                <span :style="color" v-if="(item.di0==0)&&(item.di1==0)">离线</span>
              </div>
            </mu-col>
            <mu-col span="5" offset="2">
              <div>
                <span>详情：</span>
              </div>
            </mu-col>
            <mu-col span="5" offset>
              <div>
                <a @click="details(item.devicenoname)">点击查看></a>
              </div>
            </mu-col>
          </mu-row>
        </div>
      </div>
    </div>
    <!-- <mu-container
      style="max-width: 100vw; width:100vw;"
      class="bottom"
      v-for="(item,index) in list1"
      :key="index"
    >
      <mu-bottom-nav :value.sync="shift" shift @change="change">
        <mu-bottom-nav-item
          value="0"
          title="总览"
          icon="language"
          to="/Home"
          v-show="menuArr.HomeStatus"
        ></mu-bottom-nav-item>
        <mu-bottom-nav-item
          value="1"
          title="日常管理"
          icon="event"
          to="/DayControl"
          v-show="menuArr.DayStatus"
        ></mu-bottom-nav-item>
        <mu-bottom-nav-item value="4" title="监控点" icon="visibility" to="/MonitorPoint"></mu-bottom-nav-item>
        <mu-bottom-nav-item
          value="2"
          title="运维管理"
          icon="build"
          to="/Control"
          v-show="menuArr.RunControlStatus"
        ></mu-bottom-nav-item>
        <mu-bottom-nav-item
          value="3"
          title="设置"
          icon="settings"
          to="/Settings"
          v-show="menuArr.systemStatus"
        ></mu-bottom-nav-item>
      </mu-bottom-nav>
    </mu-container> -->
    <MyFooter></MyFooter>
  </div>
</template>
<script>
import img1 from "../../../static/img/details5.jpg";
var token = sessionStorage.getItem('token')
import MyFooter from "../myFooter";
export default {
  data() {
    return {
      citys: [
        '川菜店','鲁菜店','粤菜店','湘菜店'
      ],
      filterable: {
        value1: '',
        value2: [],
        value3: []
      },
      menuArr: [
        {
          HomeStatus: false,
          DayStatus: false,
          RunControlStatus: false,
          systemStatus: false
        }
      ],
      list: [],
      color: "color:green",
      number: 0,
      msg: "hello 数据详情",
      // shift: "0",
      list1: [{ name: "总览", icon: "language" }],
      labelPosition: "top",
      form: {
        input: "",
        select: "",
        date: "",
        radio: "",
        checkbox: [],
        switch: false,
        slider: 30,
        textarea: "",
        token: ""
      },
      name: null
    };
  },
  methods: {
     fullscreen () {
       this.loading = this.$loading();
    },
    reset(){
      this.name=null
      this.filterable.value1 = ""
      this.http();
    },
    menu() {
      var HomeStatus = sessionStorage.getItem("HomeStatus");
      if (HomeStatus == "false") {
        this.menuArr.HomeStatus = false;
      } else {
        this.menuArr.HomeStatus = true;
      }
      console.log(this.menuArr.HomeStatus);
      var DayStatus = sessionStorage.getItem("DayStatus");
      if (DayStatus == "false") {
        this.menuArr.DayStatus = false;
      } else {
        this.menuArr.DayStatus = true;
      }
      var RunControlStatus = sessionStorage.getItem("RunControlStatus");
      if (RunControlStatus == "false") {
        this.menuArr.RunControlStatus = false;
      } else {
        this.menuArr.RunControlStatus = true;
      }

      var systemStatus = sessionStorage.getItem("systemStatus");
      if (systemStatus == "false") {
        this.menuArr.systemStatus = false;
      } else {
        this.menuArr.systemStatus = true;
      }
    },
    http() {
      //   var username = "root";
      //   var password = "root";
      console.log('token',token)
      var _this = this;
      var token = this.token;
      var name = this.name;
      var url = "http://47.112.16.168:8800/app/monitoring";
      // http://47.112.16.168:8800
      // http://192.168.50.150:8800
      // ?username="+username+"password="+password
      this.$http
        .post(
          url,
          {
            name
          },
          { headers: { token: token } }
        )
        .then(res => {
          console.log("res.data",res.data);
          this.list = res.data.devices;
          this.citys = res.data.name
          console.log(this.list);
          this.number = this.list.length;
          this.numberInit();
          this.loading.close();
        })
        .catch(res => {
          console.log(res);
        });
    },

    search(value) {
      // var value = this.form.input;
      this.name = value;
      this.http();
    },
    details(e) {
      //   that.$router.replace({
      //         path: "/Home",
      //         query: { id: that.index }
      //       });
      this.$router.push({ path: "/PointDetails", query: { devicenoname: e } });
    },
    // change(e) {
    //   console.log(e);
    //   this.shift = "4";
    // },
    numberInit() {
      if (this.number > 100 && this.number < 1000) {
        this.number = "0" + this.number;
      } else if (this.number > 10 && this.number < 100) {
        this.number = "0" + "0" + this.number;
      } else {
        this.number = "0" + "0" + "0" + this.number;
      }
    }
  },
  beforeCreate() {},

  created() {
    this.fullscreen(); 
    // this.token = this.$store.state.token;
    this.menu();
    this.http();
    // this.shift = "4";

    // this.menu();
  },
  mounted() {
    this.numberInit();
  },
  components: {
    MyFooter
  }
};
</script>
<style scoped>
.bottom {
  position: fixed;
  bottom: 0;
  padding: 0;
  border-top: rgb(134, 245, 167) 1px solid;
  /* margin-right: 5px; */
}
.number p {
  letter-spacing: 8px;
  font-size: 40px;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  color: rgb(168, 163, 163);
}
.header1 {
  background-image: url("../../../static/img/details5.jpg");
  width: 100vw;
  color: rgb(17, 16, 16);
  text-align: center;
  height: 100%;
}
.title {
  font-size: 20px;
  height: 40px;
  padding-top: 10px;
  /* font-weight: 600; */
}
.title span{
  font-size: 14px;
  font-weight: 700;
}
.header span {
  font-size: 16px;
}
.title1 {
  text-indent: 40px;
  font-weight: 600;
}
.dataContent {
  margin-bottom: 60px;
}
</style>



